.headerWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fcfcfc;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.nav {
    display: flex;
    align-items: center;
    height: 60px;
    max-width: 1008px;
    width: 100%;
}
.wrapper {
    display: flex;
    align-items: center;
}
.avatar {
    margin-left: 12px;
    height: 36px;
    width: 36px;
    border-radius: 50%;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
    }
}
.navList {
    padding: 0;
    margin: 0;
    display: flex;
    flex-grow: 1;
    justify-content: end;
    align-items: center;
    height: 100%;
}

.navLinkWrapper {
    height: 100%;
    color: rgba(45, 46, 70, 0.4);
    text-decoration: none;
    text-align: center;
}

.navLinkItem {
    padding: 0 44px;
    font-size: 14px;
    line-height: 16px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        color: #2d2e46;
        background: #fcfcfc;
        text-decoration: none;
    }
}

.active {
    color: #2d2e46;
    background: #fcfcfc;
    height: 100%;
    border-bottom: 3px solid #366eff;
    text-decoration: none;
    text-align: center;
}
