.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.forgotWrapper {
    margin-top: 48px;
    font-size: 14px;
    font-weight: 400;
    line-height: 16.71px;
    margin-bottom: 92px;
}
.inputWrapper input {
    margin-bottom: 24px;
}
.btns {
    margin-top: 84px;
    display: flex;
    justify-content: space-between;
}
.btnLeft {
    flex-basis: 40%;
    margin-right: 18px;
}
.btnRight {
    flex-basis: 60%;
    margin-left: 18px;
}
