.inputField {
    position: relative;
}

.textFiledLabel {
    display: flex;
    margin-bottom: 0;
    color: #24254a;
    opacity: 50%;
    font-size: 13px;
    line-height: 20px;
}

.textFiledInput {
    display: flex;
    width: 100%;
    font-family: inherit;
    font-size: 16px;
    font-weight: 500;
    color: #2d2e46;
    border: none;
    border-bottom: 1.58px solid rgba(36, 37, 74, 0.2);
    box-sizing: border-box;
    outline: none;
}

.imageEye {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 12px;
    width: 24px;
    height: 24px;

    img {
        width: 100%;
        height: 100%;
    }
}

.inputError {
    font-size: 12px;
    position: absolute;
    left: 0;
    bottom: -20px;
    color: red;
}
